import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	components: {uniLoadMore},
	data(){
		return{
			money:"",
			status:"loading",
			shopObj:{},
			salesList:[],
			pageNo:1,
			pageAll:2,
		}
	},
	methods:{
		search(){
			let that=this;
			if(that.pageNo<=that.pageAll){
				that.$ajax({
					url:"/fund/searchAllSalesRevenueForPage",
					data:{
						pageNo:that.pageNo,
						pageSize:20,
						searchData: {
						  marchantId: that.shopObj.merchantId
						},
					},
					success(d){
						that.pageNo=that.pageNo*1+1;
						that.pageAll=d.totalPage
						if(that.pageNo==2){
							that.salesList=d.lists
						}else{
							let arr=that.salesList;
							that.salesList=that.salesList.concat(d.lists)
						}
						if(that.pageNo>that.pageAll){
							that.status="noMore"
						}
					}
				})
			}
		}
	},
	onReachBottom(){
		this.search()
	},
	onLoad(e){
		this.money=e.money||0
	},
	onShow(){
		let that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
				that.search()
			}
		})
	},	
}
